.register {
  width: 100%;
  height: calc(100vh - 120px);
  grid-area: register;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 1281px) {
  .register {
    height: calc(100vh - (90/1280*100vw) - 33px);
  }
}
